html{
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
}

body{
  margin: 0;
}

.people {
  display: grid;
  padding: 30px 55px;

  grid-template-columns: repeat(2, 200px);
  gap: 40px 30px;
  justify-content: center;

  @media (min-width : 1000px) {
    grid-template-columns: repeat(4, 200px);
  }
}

.person {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;

  transition: transform 1s;

  &__photo {
    position: relative;
    height: 200px;
    width: 200px;
    margin: 0 auto;
    overflow: hidden;
    border-radius: 50%;

    transition: border-radius 1s;
  }
  
  &__photo-img {
    z-index: 1;
    width: auto;
    height: 100%;
    margin: 0 auto;
  }

  &__rank {
    right: 10px;
    top: 84px;
    width: 32px;
    height: 32px;
  
    line-height: 32px;
    font-size: 16px;
    font-weight: 700;
  
    text-align: center;
    position:absolute;
    border-radius: 50%;
    color: #8B0000;
    background-color: white;
  }

  &:hover {
    transform: scale(1.2);
    .person__details-extra{
      opacity: 1;
      text-align: center;
      background-color: 10px solid white;
    }
    .person__photo{
      border-radius: 60px;
    }
  }

  &__name {
    color: #2F4F4F;
    font-weight: 700;
  }

  &__details {
    text-align: center;

    &-extra { 
      opacity: 0;
    }
  }
}

.sport::after {
  content: ',';
}

.pay::before {
  content: '$';
}

.pay::after {
  content: 'M';
}


